<template>
    <div id="wheel-record">
        <public-header title="幸运大转盘参与记录"></public-header>
        <div class="content">
            <div class="wrap-search wrap-search-input">
                <div class="tab-group flex-lr border-1px-b">
                    <div class="tab-item" :class="{active: filter.items.type == '0'}" @click="onToggleStatus('0')">全部</div>
                    <div class="tab-item" :class="{active: filter.items.type == 'one'}" @click="onToggleStatus('one')">一等奖</div>
                    <div class="tab-item" :class="{active: filter.items.type == 'two'}" @click="onToggleStatus('two')">二等奖</div>
                    <div class="tab-item" :class="{active: filter.items.type == 'three'}" @click="onToggleStatus('three')">三等奖</div>
                    <div class="tab-item" :class="{active: filter.items.type == 'noaward'}" @click="onToggleStatus('noaward')">未中奖</div>
                </div>
                <van-search placeholder="请输入用户姓名或UID" v-model="filter.items.keyword" />
            </div>
            <van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
            <van-list
                    v-model="records.loading"
                    :finished="records.finished"
                    @load="onLoad"
                    :offset="100"
                    :immediate-check="false"
                    v-if="!records.empty"
                    class="record-list"
            >
                <div class="record-item" v-for="(item, index) in records.data" :key="item.id">
                    <div class="record-title flex-lr">
                        <div class="title flex">
                            <div class="title-img">
                                <img :src="item.avatar" alt="">
                            </div>
                            <div class="title-name">{{item.nickname}}</div>
                            <div class="font-14 c-gray padding-10-r">UID：{{item.uid}}</div>
                        </div>
                        <div class="itag itag-primary" v-if="item.award.type == 'one'">一等奖</div>
                        <div class="itag itag-info" v-else-if="item.award.type == 'two'">二等奖</div>
                        <div class="itag itag-danger" v-else-if="item.award.type == 'three'">三等奖</div>
                        <div class="itag itag-disabled" v-else-if="item.award.type == 'noaward'">未中奖</div>
                    </div>
                    <div class="record-detail border-1px-t">
                        <div class="record-info flex">
                            <div>活动名称：</div>
                            <div>{{item.activity_title}}</div>
                        </div>
                        <div class="record-info flex" v-if="item.type !== 'noaward'">
                            <div>奖项详情：</div>
                            <template v-if="item.award_type">
								<div v-if="item.award_type.name !== 'redpacket'">
									{{item.award_value}}
									<template v-if="item.award_type.name == 'credit1'">积分</template>
									<template v-else-if="item.award_type.name == 'credit2'">元</template>
									<div class="gift-type itag itag-danger">{{item.award_type.text}}</div>
								</div>
								<div v-else>
									满减红包
									<div class="gift-type itag itag-danger">{{item.award_type.text}}</div>
								</div>
							</template>
                        </div>
                        <div class="record-info flex" v-else>
                            <div>奖项详情：</div>
                            <div>未中奖</div>
                        </div>
                        <div class="record-info flex">
                            <div>抽奖时间：</div>
                            <div>{{item.addtime_cn}}</div>
                        </div>
                    </div>
                    <div class="border-1px-t btn-group" v-if="item.status == 0">
                        <div class="btn-item bg-default" @click="onChangeStatus(item, index)">设为已处理</div>
                    </div>
                </div>
                <div class="loaded" v-if="records.finished">
                    <div class="loaded-tips">没有更多了</div>
                </div>
            </van-list>
            <div class="no-data" v-else>
                <img src="static/img/order_no_con.png" alt="">
                <p>没有符合条件的数据!</p>
            </div>
            </van-pull-refresh>
        </div>
        <iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
                type: '0',
				isRefresh: false,
				showPreLoading: true,
                filter: {
					items: {
						type: '0'
                    }
                }
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/wheel/record/order'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
            },
			onChangeStatus(item, index){
				var that = this;
				that.util.jspost({
					url: 'plateform/wheel/record/status',
					data: {
						id: item.id
					},
					confirm: '确定发放赠品吗?',
					success: function(){
						that.records.data[index].status = 1;
					}
				});
            },
			onToggleStatus(value){
				if(this.filter.items.type == value) {
					return;
				}
				this.filter.items.type = value;
			}
		},
        mounted(){
			this.onLoad();
        },
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			}
		}
	}
</script>

<style>
    #wheel-record .content .record-item{
        margin-bottom: 10px;
        background-color: #fff;
    }
    #wheel-record .content .record-item .record-title{
        padding: 10px;
    }
    #wheel-record .content .record-item .title .title-img{
        width: 35px;
        height: 35px;
    }
    #wheel-record .content .record-item .title .title-img img{
        width: 100%;
        height: 100%;
        border-radius: 100%;
    }
    #wheel-record .content .record-item .title .title-name{
        padding: 0 10px;
    }
    #wheel-record .content .record-item .record-detail{
        padding: 10px;
    }
    #wheel-record .content .record-item .record-detail .record-info{
        font-size: 14px;
        color: #7a7a7a;
        padding-bottom: 8px;
    }
    #wheel-record .content .record-item .record-detail .record-info:last-child{
        padding-bottom: 0px;
    }
    #wheel-record .content .record-item .record-detail .record-info .gift-type{
        font-size: 12px;
        padding: 2px 4px;
        margin-left: 10px;
    }
    #wheel-record .content .record-item .btn-group{
        display: flex;
        flex-wrap: nowrap;
        justify-content: flex-end;
        align-items: center;
        width: 100%;
        padding: 10px;
    }
    #wheel-record .content .record-item .btn-group .btn-item{
        min-width: 60px;
        margin-left: 10px;
        text-align: center;
        padding: 10px 8px;
        border: 1px solid #e5e5e5;
        border-radius: 3px;
        font-size: 14px;
    }
</style>


